import Card from '../scripts/components/Card.js';
import FormValidator from '../scripts/components/FormValidator.js';
import Section from '../scripts/components/Section.js';
import PopupWithImage from '../scripts/components/PopupWithImage.js';
import PopupWithForm from '../scripts/components/PopupWithForm.js';
import UserInfo from '../scripts/components/UserInfo.js';
import Api from '../scripts/components/Api.js';
import PopupWithConfirm from '../scripts/components/PopupWithConfirm.js';

import './index.css';
import { data } from 'autoprefixer';

//  
const config = {
  formSelector: '.popup__form',
  inputSelector: '.popup__input',
  submitButtonSelector: '.popup__button-submit',
  inactiveButtonClass: 'popup__button-submit_disabled',
  inputErrorClass: 'popup__input_type_error',
  errorClass: 'popup__error_visible',
};

const buttonEditProfile = document.querySelector('.profile__button-edit'); //  
const formEditProfile = document.forms['editForm']; //  
const buttonAddCard = document.querySelector('.profile__button-add'); //  
const formAddCard = document.forms['addCardForm']; //  
const buttonAvatar = document.querySelector('.profile__button-avatar'); //  
const formAvatar = document.forms['avatarForm']; // 
const submitProfileEdit = document.querySelector(
  '.popup__button-submit_type_edit'
);
const submitUserAvatar = document.querySelector(
  '.popup__button-submit_type_avatar'
);
const submitAddCard = document.querySelector(
  '.popup__button-submit_type_add-card'
);
const submitConfirm = document.querySelector(
  '.popup__button-submit_type_confirm'
);

let userId;

const api = new Api({
  baseUrl: 'https://mesto.nomoreparties.co/v1/cohort-65',
  headers: {
    authorization: '9d9d46b1-d674-4200-a18f-5deb215f4e52',
    'Content-Type': 'application/json',
  },
});

//   
api
  .getUserInfo()
  .then((res) => (userId = res._id))
  .then((res) => userInfo.getUserInfo(res))
  .catch((err) => console.error(`: ${err}`));

//    
api
  .getInitialCards()
  .then((res) => cardList.renderItems(res))
  .catch((err) => console.error(`: ${err}`));

//  
const userInfo = new UserInfo({
  selectorUserName: '.profile__name',
  selectorUserAbout: '.profile__description',
  selectorUserAvatar: '.profile__image',
});

//
const profileFormValidator = new FormValidator(formEditProfile, config);
profileFormValidator.enableValidation();

const cardFormValidator = new FormValidator(formAddCard, config);
cardFormValidator.enableValidation();

const avatarFormValidator = new FormValidator(formAvatar, config);
avatarFormValidator.enableValidation();

//  
//   //
buttonEditProfile.addEventListener('click', function () {
  popupEditProfile.open();
  const profileInfo = userInfo.getUserInfo();
  popupEditProfile.setInputValues(profileInfo);
});

// 
function handleFormSubmitEdit(data) {
  api
    .editUserInfo(data)
    .then((res) => {
      userInfo.setUserInfo(res);
      submitProfileEdit.textContent = '';
      popupEditProfile.close();
    })
    .catch((err) => console.error(`: ${err}`));
  submitProfileEdit.textContent = '...';
}

//   
const popupEditProfile = new PopupWithForm(
  '.popup_type_profile',
  handleFormSubmitEdit
);
popupEditProfile.setEventListeners();

// 
// 
function handleFormSubmitAvatar(data) {
  api
    .changeUserAvatar(data)
    .then((res) => {
      userInfo.setUserInfo(res);
      submitUserAvatar.textContent = '';
      popupAvatar.close();
    })
    .catch((err) => console.error(`: ${err}`));
  submitUserAvatar.textContent = '...';
}

//  
const popupAvatar = new PopupWithForm(
  '.popup_type_avatar',
  handleFormSubmitAvatar
);
popupAvatar.setEventListeners();

//   
buttonAvatar.addEventListener('click', function () {
  cardFormValidator.resetValidation();
  popupAvatar.open();
});

//-
// -
const popupZoomImage = new PopupWithImage('.popup_type_image');
popupZoomImage.setEventListeners();

//  
function handleCardClick(name, link) {
  popupZoomImage.open(name, link);
}

//  
//   //
buttonAddCard.addEventListener('click', function () {
  cardFormValidator.resetValidation();
  popupAddCard.open();
});

//   
const popupAddCard = new PopupWithForm(
  '.popup_type_add-card',
  handleFormSubmitAdd
);
popupAddCard.setEventListeners();

//  
function createCard(data) {
  const newCard = new Card(
    data,
    userId,
    '.card-template',
    handleCardClick,
    handleLikeClick,
    handleCardDelete
  );

  return newCard.generateCard();
}

// 
const cardList = new Section(
  {
    renderer: (item) => {
      const card = createCard(item);
      cardList.setItem(card);
    },
  },
  '.elements'
);

//   
function handleFormSubmitAdd(data) {
  api
    .addCard(data)
    .then((res) => {
      const card = createCard(res);
      cardList.setItem(card);
      submitAddCard.textContent = '';
      popupAddCard.close();
    })
    .catch((err) => console.error(`: ${err}`));
  submitAddCard.textContent = '...';
}

// 
function handleLikeClick(card) {
  if (!card.isLiked()) {
    api
      .addLike(card.cardId)
      .then((res) => {
        card.changeLikes();
        card.countUserLikes(res);
      })
      .catch((err) => console.error(`: ${err}`));
  } else {
    api
      .deleteLike(card.cardId)
      .then((res) => {
        card.changeLikes();
        card.countUserLikes(res);
      })
      .catch((err) => console.error(`: ${err}`));
  }
}

// 
// 
const popupConfirm = new PopupWithConfirm('.popup_type_confirm');
popupConfirm.setEventListeners();

// 
function handleCardDelete(card) {
  const submitCardDelete = () => {
    api
      .deleteCard(card.cardId)
      .then(() => {
        card.removeCard();
        submitConfirm.textContent = '';
      })
      .catch((err) => console.error(`: ${err}`));
    submitConfirm.textContent = '...';
  };
  popupConfirm.setSubmitAction(submitCardDelete);
  popupConfirm.open();
}
